}
img {
  height: 220px;
  padding-top: 20px;
 margin-bottom: 10px;
}
.beerName{
 
    font-family: Bangers;
    letter-spacing: 2px;
    width: 90%;
    margin: auto;
    font-size: 19px;
    margin-bottom: 10px;
    text-transform: uppercase;
    min-height: 75px;
    font-weight: 200;
}

.abv{
  
    font-size: 16px;
    border: 2px solid #000;
    border-radius: 30px;
    padding: 3px 10px;
    display: inline-block;
    margin-bottom: 20px;
    letter-spacing: .3px;
 }
 .tagLine{
    font-size: 18px;
    max-width: 90%;
    margin: auto;
    margin-bottom: 10px ;
}
 .ibu{
    font-size: 12px;
    border: 2px solid #000;
    border-radius: 10px;
    padding: 3px 10px;
    display: inline-block;
    margin-bottom: 20px;
    letter-spacing: .3px;
 } 
 
.wrapListBeer {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgb(235, 235, 57);
  margin-top: 60px;

}
.wrapCard {
  margin: auto;
  height: 430px;
  margin: 15px;
  width: 200px;
  text-align: center;
  background-color: #fafbff;
  border-radius: 5px;
  box-shadow: 0 0 0 1px rgb(20 20 20 / 5%), 0 4px 12px -4px rgb(20 20 20 / 20%);
}
.wrapCard:hover {
  transform: scale(1.05);
}

.header{
   top: 0;
   display: flex;
   z-index: 1;
   justify-content: center;
   background-color: rgb(0, 0, 0);
   color:#fafbff;
   width: 100%;
   position: fixed;
   padding: 5px;
 max-height: 60px;
}
.headText{
   min-width: 150px;
   height: 50px;
   margin-left: 70px;
   text-align: left;
   color: rgb(223, 204, 41);
   text-align: left;
   width: 100%;
   font-family: Bangers;
   letter-spacing: 5px;
   font-size: 30px;
}
.wrapbtn{
 margin-right: 50px;
}
.searchBar {


  color: #fafbff;

  text-transform: uppercase;
}
.searchInput {
  border: none;
  border-bottom: 2px solid rgb(235, 216, 47);
 margin-right: 30px;
  height: 100%;
  width: 130px;
  color: rgb(209, 191, 28);
  background-color: rgb(0, 0, 0);
  font-weight: 200;
  font-size: 30px;
}
::-webkit-input-placeholder {
  color: #fff;
}

.wrapbtn{
   max-height:50px;
}
.btn {
   letter-spacing: 1.5px;
   min-width: 170px;
   font-weight: 600;
   font-size: 24px;
   padding: 5px;
   height: 50px;
   background-color: rgb(27, 24, 24);
   color: rgb(211, 193, 35);
   font-family: Bangers;
   
}


.btnImg{
   max-height: 50px;
   background-color: rgb(228, 224, 32);
   margin-right:70px;
}
.imgKey{
   max-height: 50px;
}
.forminput{
  margin-right:100px ;
   position:absolute;
   
   background-color: black;
   z-index: 1;
}
label {
  display: block;
}
.form-background {
  width: 100vw;
  height: 100vh;
  background-color: rgba(240, 228, 228, 0.9);
  position: fixed;
}
.form {
  margin-right: 100px;
  position: absolute;
  z-index: 1;
  left: 40%;
  top: 60px;
  border: none;
  border-bottom: 2px solid rgb(235, 216, 47);
  color: rgb(209, 191, 28);
  background-color: rgb(24, 18, 18);
  font-weight: 200;
  font-size: 30px;
}
.text-danger {
  font-size: 15px;
  color: red;
}
.form-control {
  width: 100%;
  padding: 10px;
  color: rgb(209, 191, 28);
  background-color: rgb(0, 0, 0);
}
.btnClose {
  margin: 10px;
  text-align: right;
}

